import Vue from 'vue'
import Vuex from 'vuex'
// use: action -> mutation -> state -> view
// define: view -> state -> mutation -> action

const types = { // define symbol, msg name
  UPDATE_THISYEAR: "UPDATE_THISYEAR",
  UPDATE_YEARS: "UPDATE_YEARS",
  UPDATE_SELECTEDINSTITUTION: "UPDATE_SELECTEDINSTITUTION",
  UPDATE_SELECTEDPROVINCE: "UPDATE_SELECTEDPROVINCE",
  UPDATE_PROVINCEFLAG: "UPDATE_PROVINCEFLAG",
  UPDATE_PROVINCEDATA: "UPDATE_PROVINCEDATA",
  UPDATE_MINVALUE: "UPDATE_MINVALUE",
  UPDATE_MAXVALUE: "UPDATE_MAXVALUE",
  UPDATE_ZEROCOLOR: "UPDATE_ZEROCOLOR",
  UPDATE_MAPCOLORS: "UPDATE_MAPCOLORS",
  UPDATE_BINARRAY: "UPDATE_BINARRAY",
  UPDATE_TIMELINEFLAG: "UPDATE_TIMELINEFLAG",
  UPDATE_TIMELINEDICT: "UPDATE_TIMELINEDICT",
  UPDATE_INSTITUTIONDATA: "UPDATE_INSTITUTIONDATA",
  UPDATE_TOTALPROVINCE: "UPDATE_TOTALPROVINCE",
  UPDATE_PROJECTION:"UPDATE_PROJECTION"
};

Vue.use(Vuex)
// step1
export default new Vuex.Store({
state : { // states
  Zerocolor: "rgb(200,200,200)",
  Mapcolors: ["rgba(240, 249,232)", "rgba(204,235,197)", "rgba(123, 204, 196)", "rgba(67, 162, 202)", "rgba(8, 104, 172)"],
  binArray: [0, 10, 30, 50, 100, 200,500],
  TimeLineDict: {
    "count": "数量",
    "rank": "排名"
  },
  thisYear: "2020",
  SelectedProvince: "",
  SelectedInstitution: "",
  ProvinceData: {},
  InstitutionData: {},
  TotalProvince:{},
  Minvalue: 0,
  Maxvalue: 0,
  TimeLineFlag: "count", //"count"
  ProvinceFlag: "stacked", //"stacked" / "align"
  projection:{}
},

mutations:{ // define change state function (sync)
  [types.UPDATE_THISYEAR](state, payload) {
    state.thisYear = payload
  },
  [types.UPDATE_PROJECTION](state, payload) {
    state.projection = payload
  },
  [types.UPDATE_SELECTEDINSTITUTION](state, payload) {
    state.SelectedInstitution = payload
  },
  [types.UPDATE_SELECTEDPROVINCE](state, payload) {
    state.SelectedProvince = payload
  },
  [types.UPDATE_PROVINCEFLAG](state, payload) {
    state.ProvinceFlag = payload
  },
  [types.UPDATE_PROVINCEDATA](state, payload) {
    state.ProvinceData = payload
  },
  [types.UPDATE_MINVALUE](state, payload) {
    state.Minvalue = payload
  },
  [types.UPDATE_MAXVALUE](state, payload) {
    state.Maxvalue = payload
  },
  [types.UPDATE_ZEROCOLOR](state, payload) {
    state.Zerocolor = payload
  },
  [types.UPDATE_MAPCOLORS](state, payload) {
    state.Mapcolors = payload
  },
  [types.UPDATE_BINARRAY](state, payload) {
    state.binArray = payload
  },
  [types.UPDATE_TIMELINEFLAG](state, payload) {
    state.TimeLineFlag = payload
  },
  [types.UPDATE_TIMELINEDICT](state, payload) {
    state.TimeLineDict = payload
  },
  [types.UPDATE_INSTITUTIONDATA](state, payload) {
    state.InstitutionData = payload
  },
  [types.UPDATE_TOTALPROVINCE](state, payload) {
    state.TotalProvince = payload
  }
},
actions : { // define commit symbol with payload function (sync | async)
  updatethisYear({ commit }, payload) {
    commit(types.UPDATE_THISYEAR, payload)
  },
  updateprojection({ commit }, payload) {
    commit(types.UPDATE_PROJECTION, payload)
  },
  updateTotalProvince({ commit }, payload) {
    commit(types.UPDATE_TOTALPROVINCE, payload)
  },
  updateSelectedInstitution({ commit }, payload) {
    commit(types.UPDATE_SELECTEDINSTITUTION, payload)
  },
  updateSelectedProvince({ commit }, payload) {
    commit(types.UPDATE_SELECTEDPROVINCE, payload)
  },
  updateProvinceFlag({ commit }, payload) {
    commit(types.UPDATE_PROVINCEFLAG, payload)
  },
  updateProvinceData({ commit }, payload) {
    commit(types.UPDATE_PROVINCEDATA, payload)
  },
  updateMinvalue({ commit }, payload) {
    commit(types.UPDATE_MINVALUE, payload)
  },
  updateMaxvalue({ commit }, payload) {
    commit(types.UPDATE_MAXVALUE, payload)
  },
  updateZerocolor({ commit }, payload) {
    commit(types.UPDATE_ZEROCOLOR, payload)
  },
  updateMapcolors({ commit }, payload) {
    commit(types.UPDATE_MAPCOLORS, payload)
  },
  updatebinArray({ commit }, payload) {
    commit(types.UPDATE_BINARRAY, payload)
  },
  updateTimeLineFlag({ commit }, payload) {
    commit(types.UPDATE_TIMELINEFLAG, payload)
  },
  updateTimeLineDict({ commit }, payload) {
    commit(types.UPDATE_TIMELINEDICT, payload)
  },
  updateInstitutionData({ commit }, payload) {
    commit(types.UPDATE_INSTITUTIONDATA, payload)
  }
},
modules: {}
})



