<template>
  <div id="mainview">
    <div class="container">
        <div id="MapTooltip" class="tooltip"></div>
        <div id="ProvinceBarTooltip" class="tooltip"></div>
        <div id="InstitutionTooltip" class="tooltip"></div>
        <div id="TimeLineTooltip" class="tooltip"></div>

        <div class="row" style="background: lightblue; padding: 0.5%;">
            <div id="header"
                class="col-xs-4 col-sm-4 col-md-4 col-lg-4 col-xs-offset-4 col-sm-offset-4 col-md-offset-4 col-lg-offset-4">
                ChinaVis2020 注册信息分布</div>
            <ol id="ChinaVisContainer" class="col-xs-4 col-sm-4 col-md-4 col-lg-4">
              <li class="ChinaVis" @click="updatethisYear(year)" v-for="year in Years">{{ year }}</li>
            </ol>
        </div>
        <div class="row">
            <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="row">
                    <div id="leftcolumn" class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                        <div id="MapHeadContainer">
                            <div id="MapHead" class="ViewTitle"></div>
                            <div id="TotalNum" class="ViewSubTitle"></div>
                            <Map></Map>
                        </div>
                    </div>
                </div>
                <!-- <div class="row">
                    <div id="leftcolumn2" class="col-xs-12 col-sm-12 col-md-8 col-lg-8">
                        <div id="TimeLineContainer">
                            <div id="TimeLineHead" class="ViewTitle"></div>
                            <TimeLine></TimeLine>
                        </diV>
                    </div>
                </div> -->
            </div>
            <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="row">
                    <div id="rightcolumn1" class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                        <div class="BarHeadContainer">
                            <div id="ProvinceBarHead" class="ViewTitle"></div>
                            <div id="ProvinceNum" class="ViewSubTitle"></div>
                           <ProvinceBar></ProvinceBar>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div id="rightcolumn2" class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                        <div class="BarHeadContainer">
                            <div id="InstitutionBarHead" class="ViewTitle"></div>
                            <div id="InstitutionNum" class="ViewSubTitle"></div>
                            <InstitutionBar></InstitutionBar>
                        </div>
                        <button id="return">返回</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import $ from "jquery";
import * as d3 from "d3";
import InstitutionBar from '../components/InstitutionBar.vue'
import ProvinceBar from '../components/ProvinceBar.vue'
import global from "@/constants/global"
import Map from '../components/Map.vue'

export default {
  data() {
    return {
      Years: global.Years,
      // selectedDay: this.$store.getters.selectedDay,
    }
  },
  components: {
    Map,
    ProvinceBar,
    InstitutionBar,
  },

  computed: {
    ...mapState(["thisYear", "Minvalue","Maxvalue","SelectedProvince","SelectedInstitution","ProvinceData","InstitutionData","TimeLineFlag","ProvinceFlag","TotalProvince"]),
  },

  watch: {
    thisYear: function (val) {
      $("#header").text("ChinaVis" + this.thisYear + " 注册信息分布")
      $('#TotalNum').text('注册人数：' + String(global.MemberList[val].length));
      console.log(val);
    }
  },
  mounted() {
    this.ReLoadData()
    this.updatethisYear("2019");
  },
  methods: {
    ...mapActions(["updatethisYear","updateMaxvalue","updateProvinceData","updateInstitutionData","updateTotalProvince","updateProvinceData","updateProvinceData"]),
    ReLoadData() {
      let self = this;
      let ProvinceData = self.ProvinceData;
      let InstitutionData = self.InstitutionData;
      let TotalProvince = self.TotalProvince;
      let Maxvalue = self.Maxvalue;
      //初始化ProvinceData
      for (let y in global.Years) {
        ProvinceData[global.Years[y]] = {}
        for (let i in global.ProvinceList) {
          ProvinceData[global.Years[y]][global.ProvinceList[i]] = {
            'count': 0,
            //'color': 'rgb(255,255,255)',
            //'region': Province_dict[global.ProvinceList[i]],
            'Institution': {},
            'Property': {}
          }
        }
      }
      //统计ProvinceData
      for (let y in global.Years) {
        for (let i in global.MemberList[global.Years[y]]) {
          let danwei = global.MemberList[global.Years[y]][i].danwei
          if (global.Institution_dict.hasOwnProperty(danwei)) {
            let province = global.Institution_dict[danwei].Province
            let short_danwei = global.Institution_dict[danwei].short //处理统一都用short
            let Type = global.Institution_dict[danwei].Type //处理统一都用short
            if (ProvinceData[global.Years[y]].hasOwnProperty(province)) {
              ProvinceData[global.Years[y]][province].count += 1
              if (ProvinceData[global.Years[y]][province]['Institution'].hasOwnProperty(short_danwei)) {
                ProvinceData[global.Years[y]][province]['Institution'][short_danwei] += 1
              } else {
                ProvinceData[global.Years[y]][province]['Institution'][short_danwei] = 1
              }
              if (ProvinceData[global.Years[y]][province]['Property'].hasOwnProperty(Type)) {
                ProvinceData[global.Years[y]][province]['Property'][Type] += 1
              } else {
                ProvinceData[global.Years[y]][province]['Property'][Type] = 1
              }
            } else {
              //console.log('海外：' + danwei)
            }
          } else if (danwei != "" && danwei != "test") {
            console.log('未定义：' + danwei)
          }
        }
      }
      let maxvalues = {};
      for (let y in global.Years) {
        let temp = 0
        TotalProvince[global.Years[y]] = 0
        for (let i in ProvinceData[global.Years[y]]) {
          let cnt = ProvinceData[global.Years[y]][i].count
          if (cnt != 0) {
            temp = Math.max(cnt, temp)
            TotalProvince[global.Years[y]] += 1
          }
        }
        maxvalues[global.Years[y]] = temp
      }
      Maxvalue = Math.max(...Object.values(maxvalues))

      //统计InstitutionData
      for (let y in global.Years) {
        InstitutionData[global.Years[y]] = {}
        for (let i in global.MemberList[global.Years[y]]) {
          let danwei = global.MemberList[global.Years[y]][i].danwei
          let short_danwei = danwei
          if (danwei == "" || danwei == "test") continue
          if (global.Institution_dict.hasOwnProperty(danwei))
            short_danwei = global.Institution_dict[danwei].short //处理统一都用short
            let type = global.Institution_dict[danwei].Type
          if (InstitutionData[global.Years[y]].hasOwnProperty(short_danwei)) {
            InstitutionData[global.Years[y]][short_danwei].count += 1
            //InstitutionData[global.Years[y]][short_danwei].people.push(global.MemberList[global.Years[y]][i].name)
          } else {
            if (global.Institution_dict.hasOwnProperty(danwei)) {
              let province = global.Institution_dict[danwei].Province
              InstitutionData[global.Years[y]][short_danwei] = {
                'count': 1,
                'Province': province,
                'Property': type
                //'region': Province_dict[province],
                //'people': [global.MemberList[global.Years[y]][i].name]
              }
            } else if (danwei != "" && danwei != "test") {
              InstitutionData[global.Years[y]][short_danwei] = {
                'count': 1,
                'Province': '未知',
                'Property': '未知'
                //'region': '未知',
                //'people': [global.MemberList[global.Years[y]][i].name]
              }
            }
          }
        }
      }
      self.updateProvinceData(ProvinceData);
      self.updateInstitutionData(InstitutionData);
      self.updateTotalProvince(TotalProvince);
      self.updateMaxvalue(Maxvalue);
      console.log(self.ProvinceData)
    },
  },
  created() {
  }
}

</script>
<style scoped lang="less">
@import "../style/base/style.less";
@import "../style/base/iview.less";

#mainview {
  position: absolute;
  width: 100%;
  height: 100%;
}

</style>



