// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import Vuex from 'vuex'
Vue.use(Vuex)

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import App from './App';
import router from './router';
import store from './vuex/index' 
import 'font-awesome/css/font-awesome.css'

import './assets/iconfont/iconfont.js'


import iView from 'iview';
import 'iview/dist/styles/iview.css';
Vue.use(iView);

//import style from 'style';
//import '../src/style/style.css';
//Vue.use(style);

import $ from 'jquery'
window.$ = $

import * as d3 from 'd3'
window.d3 = d3

import VueResource from 'vue-resource'
Vue.use(VueResource)

// import


router.beforeEach((to, from, next) => {
    iView.LoadingBar.start();
    next();
});

router.afterEach(route => {
    iView.LoadingBar.finish();
});

Vue.config.productionTip = false;

// make global
import utils from './commons/utils.js'
Vue.prototype.$utils = utils
import api from './api/index.js'
window.api = api

import global_ from './commons/config'//引用文件
Vue.prototype.GLOBAL = global_//挂载到Vue实例上面

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
});

Vue.prototype.compare = function(p) { //这是比较函数
  return function (m, n) {
  var a = m[p];
  var b = n[p];
  return b - a; //大的在前
}
}

 Vue.prototype.GetMax = function(obj, key) {
  let max = 0
  for (i in obj)
      if (obj[i][key] > max)
          max = obj[i][key]
  return max
}

 Vue.prototype.DictFilter = function(obj, key, value) {
  tmp = {}
  for (item in obj)
      if (obj[item][key] == value)
          tmp[item] = obj[item]
  return tmp
}

 Vue.prototype.GetRank = function(data, key, target) { //外部确保字典中存在该target
  let aim = data[target][key]
  let rank = 1
  for (item in data) { //不必排除本身
      if (data[item][key] > aim) {
          rank += 1
      }
  }
  return rank
}

 Vue.prototype.DictFilter1 = function(obj, key, value) {
  tmp = {}
  for (item in obj) {
      if (Institution_dict.hasOwnProperty(obj[item][key]) && Institution_dict[obj[item][key]].Province == value) {
          tmp[item] = obj[item]
      }
  }
  return tmp
}

 Vue.prototype.DictEliminate = function (obj, key, value) {
  tmp = {}
  for (item in obj)
      if (obj[item][key] != value)
          tmp[item] = obj[item]
  return tmp
}

 Vue.prototype.CreateTooltip = function(id, count) {
  return "<h4>" + id + "</h4><table>" +
      "<tr><td>数量</td><td>" + (count) + "人</td></tr>" +
      "</table>";
}

 Vue.prototype.CreateTooltip1 = function(Flag, id, count) {
  return "<h4>" + id + "</h4><table>" +
      "<tr><td>" + TimeLineDict[Flag] + "</td><td>" + (count) + "人</td></tr>" +
      "</table>";
}

 Vue.prototype.CreateTooltip2 = function(type, id, count) {
  return "<h4>" + id + "</h4><table>" +
      "<tr><td>" + type + "</td><td>" + (count) + "人</td></tr>" +
      "</table>";
}


