<template>
  <div class='map'>
  </div>
</template>
<script>
import * as d3 from 'd3'
import { mapState, mapActions } from "vuex";
import global from "@/constants/global"
const cn = require('../../static/geojson/china.json')
const nanhai = require('../../static/geojson/nanhai.json')

export default {
  name: 'Map',
  data() {
    return {
      svg: {},
      projection: null,
      SvgWidth: 0,
      SvgHeight: 0,
    }
  },
  computed: {
    ...mapState([
      "SelectedProvince",
      "ProvinceData",
      "thisYear",
      "binArray",
      "Mapcolors",
      "Zerocolor",
    ]),
  },
  watch: {
    // ProvinceData: function(oldval, newval) {
    //   let self = this
    //   console.log('here:', oldval, newval)
    //   if (newval == {}) return
    //   self.UpdateMap()
    // },
    ProvinceData: function() {
      this.UpdateMap()
    },
    SelectedProvince: function(val) {
      let self = this
      console.log(val)
    },
    thisYear: function(val) {
      let self = this
      self.UpdateMap()
    }
  },
  mounted() {
    let self = this
    self.InitMap(cn, nanhai)
  },
  methods: {
    ...mapActions(["updateprojection"]),
    GetColor(value) {
      let self = this;
      if (value == 0) {
        return self.Zerocolor
      } else if (value <= self.binArray[1]) {
        return self.Mapcolors[0]
      } else if (value <= self.binArray[2]) {
        return self.Mapcolors[1]
      } else if (value <= self.binArray[3]) {
        return self.Mapcolors[2]
      } else if (value <= self.binArray[4]) {
        return self.Mapcolors[3]
      }
      return self.Mapcolors[4];
    },
    InitMap(cn, nanhai) {
      let self = this
      let SvgWidth = $("#leftcolumn").width() * 0.5;
      let SvgHeight = $("#leftcolumn").width() * 0.45;
      self.SvgWidth = SvgWidth
      self.SvgHeight = SvgHeight
      $('#Mapsvg').remove();
      $('#MapHead').text('中国境内注册人员地区分布')
      self.svg = d3.select('#leftcolumn')
        .append('svg') //   添加svg画布
        .attr("preserveAspectRatio", "xMidYMid meet")
        .attr("viewBox", `0 0 ${SvgWidth} ${SvgHeight}`)
        .attr('id', "Mapsvg")

    var map = self.svg.append('g')
        .attr('id', "map")
        .attr('transform', function() {
            return `translate(${0},${0})`
        })
        .attr('width', SvgWidth)
        .attr('height', SvgHeight)

      let projection = d3.geoMercator().fitSize([SvgWidth, SvgHeight], cn);
      var path = d3.geoPath().projection(projection);

      // self.updateprojection(projection)
      self.projection = projection
      map.append("g")
        .attr('class', 'region')
        .selectAll("path")
        .data(cn.features)
        .enter()
        .append("path")
        .attr("class", function(d) {
          return (self.Long2Short(d.properties.name) + " state")
        })
        .attr("d", path)

      for (let i = 0; i < 34; i++) {
        cn.features[i].properties.cp = self.adjustCoordinate(cn.features[i].properties.name, cn.features[i].properties.cp)
      }

      let legendX = SvgWidth * 0.038;
      let legendY = SvgHeight * 0.75;
      let legendWidth = SvgWidth * 0.1;
      let legendHeight = legendWidth * 0.22;
      let legendText = ['0', '1-10', '11-30', '31-50', '51-100', '>100']

      var legend = self.svg.selectAll(".legend")
        .data(self.binArray.slice(0, -1))
        .enter()
        .append("g")
        .attr("class", "legend")

      legend.append("rect")
        .attr("x", function(d, i) {
          return legendX;
        })
        .attr("y", function(d, i) {
          return legendY + legendHeight * i;
        })
        .attr("width", legendWidth)
        .attr("height", legendHeight)
        .style("fill", function(d, i) {
          if (i == 0) return self.Zerocolor;
          return self.Mapcolors[i - 1];
        });

      self.svg.selectAll(".valueText")
        .data(self.binArray)
        .enter()
        .append("text")
        .attr("class", "valueText")
        .attr("x", function(d, i) {
          return legendX + (legendWidth * 0.5)
        })
        .attr("y", function(d, i) {
          return legendY + legendHeight * (i + 0.2)
        })
        .style("font-size", SvgWidth / 55)
        .text(function(d, i) {
          return legendText[i];
        })
        .style('text-anchor', 'middle')
        .style('dominant-baseline', 'hanging')

      self.svg.append("text")
        .attr("class", "legendTitle")
        .attr("x", legendX + (legendWidth * 0.5))
        .attr("y", legendY - legendHeight * 1)
        .style("font-size", SvgWidth / 55)
        .text("人数")
        .style('text-anchor', 'middle')
        .style('dominant-baseline', 'hanging')

      let w = 0.09 * SvgWidth;
      let h = w * 1.2
      var svg_nanhai = self.svg.append('g')
        .attr('id', "south-china-sea-canvas")
        .attr('transform', function() {
          return `translate(${ 0.8 * SvgWidth},${ 0.82 * SvgHeight})`
        })
        .attr('width', w)
        .attr('height', h)
      self.renderSouthChinaSea(svg_nanhai, w, h, nanhai);
    },

    Long2Short(a) {
      if (a == "内蒙古自治区")
        return "内蒙古"
      if (a == "黑龙江省")
        return "黑龙江"
      return a.slice(0,2)
    },

    UpdateMap() {
      let self = this
      let ProvinceData = self.ProvinceData
      let thisYear = self.thisYear
      let Zerocolor = self.Zerocolor
      let SvgWidth = self.SvgWidth
      let SvgHeight = self.SvgHeight
      let map = self.svg
        .select('#map')
      let svg_nanhai = d3.select(self.$el)
        .select('#south-china-sea-canvas')
      
      map.selectAll("path")
        .attr("fill", function(d) {
            return self.GetColor(self.ProvinceData[thisYear][self.Long2Short(d.properties.name)].count);
        })

      let layer_prov = self.svg.selectAll('layer_prov')
        .data(cn.features)
        .enter()
        .append('g')
        .attr('class', 'layer_prov')
      
      layer_prov.append('text')
        .attr('class', 'provinceName')
        .text(function(d) {
          let key = self.Long2Short(d.properties.name)
          if (ProvinceData[thisYear][key].count == 0)
            return ""
          return key
        })
        .attr('x', function(d) {
          return self.projection(d.properties.cp)[0];
        })
        .attr('y', function(d) {
          return self.projection(d.properties.cp)[1];
        })
        .style('font-size', SvgWidth / 75)
        .style('text-anchor', 'middle')
        .style('dominant-baseline', 'hanging')
        .style('opacity', 1)
        .attr('fill', 'rgb(30,30,30)')
        .attr('pointer-event', 'none')

      layer_prov.append('text')
        .attr('class', 'provValue')
        .text(function(d) {
          let key = d.properties.name.slice(0, 2)
          if (d.properties.name == '内蒙古自治区')
            key = '内蒙古'
          if (d.properties.name == '黑龙江省')
            key = '黑龙江'
          if (self.ProvinceData[self.thisYear][key].count == 0)
            return ""
          return self.ProvinceData[self.thisYear][key].count
        })
        .attr('x', function(d) {
          return self.projection(d.properties.cp)[0];
        })
        .attr('y', function(d) {
          return self.projection(d.properties.cp)[1];
        })
        .attr('dy', '-0.5rem')
        .style('font-size', SvgWidth / 75)
        .style('text-anchor', 'middle')
        .style('dominant-baseline', 'hanging')
        .style('color', '#2C3E50')
        .style('opacity', 1)
        .attr('pointer-event', 'none')

      var location = {
        "2014": { "name": "北京", "loc": [116.8, 40.76] },
        "2015": { "name": "天津", "loc": [117.2, 39.13] },
        "2016": { "name": "长沙", "loc": [113.00, 28.22] },
        "2017": { "name": "青岛", "loc": [120.33, 36.07] },
        "2018": { "name": "上海", "loc": [121.43, 34.5] },
        "2019": { "name": "成都", "loc": [104.06, 30.67] },
        "2020": { "name": "西安", "loc": [108.95, 34.27] }
      };

      map.append('circle')
        .attr('cx', function(d) {
          return self.projection(location[self.thisYear]['loc'])[0]
        })
        .attr('cy', function(d) {
          return self.projection(location[self.thisYear]['loc'])[1]
        })
        .attr('r', 2)
        .attr('fill', 'red')
      map.append('text')
        .attr('class', 'holdCity')
        .attr('x', function(d) {
          return self.projection(location[self.thisYear]['loc'])[0]
        })
        .attr('y', function(d) {
          return self.projection(location[self.thisYear]['loc'])[1]
        })
        .attr('dy', '-0.5rem')
        .style('font-size', SvgWidth / 75)
        .text(location[self.thisYear]['name'])
        // .style('text-anchor', 'middle')
        .style('dominant-baseline', 'hanging')

      self.Initialliti();
      let liti_id = 0;
      d3.select('.region')
        .selectAll('path')
        .style('opacity', function() {
          self.Callliti(this, 0, -2, liti_id);
          liti_id += 1;
          return 1;
        })

      svg_nanhai.selectAll('.g-small-province')
        .selectAll('path')
        .attr("fill", function(d) {
          if (d.properties.name == '九段线')
            return 'transparent'
          else if (data[d.properties.name.slice(0, 2)].count == 0)
            return "#FFFFFF"
          else
            return GetColor(data[d.properties.name.slice(0, 2)].count);
          return "FFFFFF"
        })

    },

    renderSouthChinaSea(svg_nanhai, w, h, nanhai) {
      let objArr = nanhai.features
      let min_edge = w
      let center_location = [114.4, 13.2]
      svg_nanhai.append("rect")
        .attr("id", "map_nanhai_box")
        .attr("width", w)
        .attr("height", h)
        .attr('fill', 'transparent')
        .attr('stroke', '#888')

      svg_nanhai.append("clipPath") // define a clip path
        .attr("id", "clip-path-nanhai") // give the clipPath an ID
        .append("rect")
        .attr("width", w)
        .attr("height", h)
      let range_nanhai = 3.3
      let projection_nanhai = d3.geoMercator()
        .center(center_location)
        .scale(min_edge * range_nanhai)
        .translate([w / 2, h / 2]);

      let path_nanhai = d3.geoPath().projection(projection_nanhai);
      let g_nanhai = svg_nanhai.append('g').attr('class', 'g-nanhai')
        .attr("clip-path", "url(#clip-path-nanhai)")

      g_nanhai.selectAll(".g-small-province")
        .data(objArr)
        .enter()
        .append("g")
        .attr('class', 'g-small-province')
        .attr("id", d => "map-small-province-" + d.properties.id)
        .append("path")
        .attr("class", "small-province")
        .attr("id", d => "map-path-small-" + d.properties.id)
        .attr("stroke", "#aaa")
        // .attr("stroke-width", 1)
        .attr("fill", function(d) {
          if (d.properties.name == '九段线')
            return 'transparent'
          // else if (data[d.properties.name.slice(0, 2)].count == 0)
          //   return "#FFFFFF"
          // else
          //   return GetColor(data[d.properties.name.slice(0, 2)].count);
          return "FFFFFF"
        })
        .attr("d", path_nanhai)
        .attr("provinceName", d => d.properties.name)
      // 文字
      svg_nanhai.append("text")
        .attr("id", "map_nanhai_text")
        .attr("x", w)
        .attr("y", h)
        .text('南海诸岛')
        .style("font-size", w / 8)
        .attr('dx', -w * 0.03)
        .attr('dy', -h * 0.06)
        .attr('text-anchor', 'end')

      // nanhai_svg.select("#九段线")
      //   .style('stroke-width', nanhai_width/40)
      //   .style("stroke", "#ddd")
      //   .attr("class", "nine_line")

    },

    Callliti(path, dx, dy, id) {
      let self = this
      let SelectedProvince = self.SelectedProvince
      let str = d3.select(path)
        .attr('d')
      let components = str.split('M')
      for (let i = 1; i < components.length; i++) {
        self.Transform2liti(path, dx, dy, id, components[i])
      }
      let color = d3.select(path)
        .attr('fill')
      // console.log(datastr)
      let colorArr = color.split(',')
      if (colorArr.length >= 4)
        color = colorArr[0] + ',' + colorArr[1] + ',' + colorArr[2] + ')'
      d3.timeout(function() {
        let temp = d3.select(path)
          .clone(true)
          .attr('class', 'newcontour')
          .attr('fill', color)
          .attr('stroke', '#aaa')
          .attr('stroke-width', 0.5)
          .attr('transform', 'translate(' + dx + ',' + dy + ')')
          // .on('mouseout', function() {
          //     d3.selectAll('.newcontour')
          //         .remove()
          //     d3.selectAll('.litismallparts')
          //         .remove()
          // })
          .on("mouseover", function(d) {
            id = global.LongtoShort[d.properties.name]
            // d3.select("#MapTooltip")
            //     .transition()
            //     .duration(200)
            //     .style("opacity", 0.9);
            // d3.select("#MapTooltip")
            //     .html(CreateTooltip(id, self.ProvinceData[self.thisYear][id].count))
            //     .style("left", (d3.event.pageX) + "px")
            //     .style("top", (d3.event.pageY - 28) + "px");
            if (!SelectedProvince) {
              $(".state").removeClass("active");
              $(this).addClass("active");
            }
          })
          .on("mouseout", function(d) {
            if (!SelectedProvince) {
              $(".state").removeClass("active");
            }
            // d3.select("#MapTooltip")
            //     .transition()
            //     .duration(500)
            //     .style("opacity", 0);
          })
          .on('click', function(d) {
            // name = LongtoShort[d.properties.name]
            // value = self.ProvinceData[self.thisYear][name].count
            // if (value > 0) {
            //   if (SelectedProvince != name) {
            //     $(".state").removeClass("active");
            //     $("." + name + ".Provincebar").addClass("active");
            //     $(".Provincebar").removeClass("active");
            //     SelectedProvince = name
            //     //$(".state").addClass("inactive");
            //     //$(this).removeClass("inactive");
            //     $(".state").css("fill", self.Zerocolor);
            //     $(this).css("fill", GetColor(value));
            //   } else if (self.SelectedProvince == name) {
            //     returnChina(self.thisYear, self.ProvinceData, self.InstitutionData)
            //     //$(".state").removeClass("inactive");
            //     $(".state").css("fill", function() {
            //       thisname = $(this).attr('class').replace(" state", "").replace(" active", "")
            //       $(this).css("fill", self.GetColor(self.ProvinceData[self.thisYear][thisname].count));
            //     })
            //   }
            // }
            //console.log(document.getElementsByClassName(SelectedProvince+' Provincebar'))
            //let el = document.getElementsByClassName(SelectedProvince + ' Provincebar')[0]
            //console.log(el)
            //el.click();
            //console.log(d3.select('.'+SelectedProvince + '.Provincebar')[0])
            //d3.select('.'+SelectedProvince + '.Provincebar')[0].click();
          });
        d3.select('#liticontour').insert(() => temp.node(), null)
      }, 100)
    },

    Initialliti() {
      // d3.timeout(function() {
      d3.select('.region')
        .append('g')
        .attr('class', 'liti')
        .append('g')
        .attr('id', 'litiparts')
        .attr('pointer-event', 'none') //立体部分
      d3.select('.liti')
        .append('g')
        .attr('id', 'liticontour')
        .style('pointer-event', 'none') // 上层
      // }, 100)
      let pathArr = d3.select('.region')
        .selectAll('path') //立体对象

      let minArr = []
      // minArr = Arrangeliti(pathArr._groups[0]) // 排序
      minArr = pathArr._groups[0]

    },

    Arrangeliti(pathArr) {
      let tempstr = ""
      let tempdata = []
      let minArr = []
      for (let i = 0; i < pathArr.length; i++) {
        tempstr = d3.select(pathArr[i])
          .attr('d')
        tempdata = tempstr.slice(1).split('L')
        minArr.push([Getmin(tempdata), i])
      }
      let temp = [0, 0]
      for (let i = 0; i < minArr.length - 1; i++) {
        for (let j = 0; j < minArr.length - i - 1; j++) {
          if (minArr[j][0] > minArr[j + 1][0]) {
            temp[0] = minArr[j][0]
            minArr[j][0] = minArr[j + 1][0]
            minArr[j + 1][0] = temp[0]
            temp[1] = minArr[j][1]
            minArr[j][1] = minArr[j + 1][1]
            minArr[j + 1][1] = temp[1]
          }
        }
      }
      return minArr
    },

    Transform2liti(path, dx, dy, id, str) {
      let self = this
      let datastr = str.split('L')
      let x = 0
      let y = 0
      let minx = 10000
      let maxx = 0
      let mini = 0
      let maxi = 0
      let color = d3.select(path)
        .attr('fill')
      // console.log(datastr)
      let colorArr = color.split(',')
      if (colorArr.length >= 4)
        color = colorArr[0] + ',' + colorArr[1] + ',' + colorArr[2] + ')'
      for (let i = 0; i < datastr.length; i++) {
        x = parseFloat(datastr[i].split(',')[0])
        y = parseFloat(datastr[i].split(',')[1])
        if (x < minx) {
          minx = x
          mini = i
        }
        if (x > maxx) {
          maxx = x
          maxi = i
        }
        datastr[i] = [x, y]
      }
      let checkpointarray = []
      for (let i = 0; i < datastr.length; i++) {
        if (self.checkconvex(datastr, i)) checkpointarray.push(i)
      }
      let newstr = 'M'
      let movetag = 0
      let nexti = (mini + 1) % datastr.length
      let lasti = (mini - 1 + datastr.length) % datastr.length
      if (datastr[nexti][1] > datastr[lasti][1]) {
        //顺时针
        if (mini < maxi) movetag = 1
      } else {
        //逆时针
        if (mini > maxi) movetag = 1
      }
      // console.log(minx, maxx, movetag)
      let k = 0
      // console.log(checkpointarray)
      for (let i = 0; i < datastr.length; i++) {
        if (i != 0) newstr = newstr + 'L'
        newstr = newstr + (datastr[i][0]) + ',' + (datastr[i][1] + movetag * dy)
        if (datastr[i][0] == maxx || datastr[i][0] == minx) {
          movetag = (movetag + 1) % 2
          newstr = newstr + 'L' + datastr[i][0] + ',' + (datastr[i][1] + movetag * dy)
        }
        if (i == checkpointarray[k]) {
          // nexti = (i+1)%datastr.length
          // if ((Math.abs(datastr[nexti][0] - datastr[i][0]) > 10 || Math.abs(datastr[nexti][1] - datastr[i][1]) > 10)) {
          //     console.log(id, 'here')
          // } else {
          self.addpart(datastr, checkpointarray, k, id, movetag, dy)
          k = k + 1
          // }
        }
      }

      let contourstr = "M" + datastr[0][0] + ',' + (datastr[0][1] + dy)
      for (let i = 1; i < datastr.length; i++) {
        contourstr = contourstr + 'L' + datastr[i][0] + ',' + (datastr[i][1] + dy)
      }
      // console.log(newstr)
    },

    checkconvex(a, i) {
      let next_i = (i + 1) % a.length
      let last_i = (i - 1 + a.length) % a.length
      if ((a[next_i][0] - a[i][0]) * (a[i][0] - a[last_i][0]) <= 0) {
        return 1
      }
      return 0
    },

    addpart(a, b, k, id, movetag, dy) {
      // let threadhold = 8
      let i = b[k]
      let endpoint = b[(k + 1) % b.length]
      let partstr = 'M' + a[i][0] + ',' + a[i][1]
      let nexti = i
      while (i != endpoint) {
        partstr = partstr + 'L' + a[i][0] + ',' + (a[i][1] + dy)
        nexti = (i + 1) % a.length
        // if ((Math.abs(a[nexti][0] - a[i][0]) > threadhold || Math.abs(a[nexti][1] - a[i][1]) > threadhold)) {
        //     return
        // } else {
        i = nexti
        // }
      }
      partstr = partstr + 'L' + a[i][0] + ',' + (a[i][1] + dy)
      while (i != b[k]) {
        partstr = partstr + 'L' + a[i][0] + ',' + a[i][1]
        i = (i - 1 + a.length) % a.length
      }
      partstr = partstr + 'L' + a[i][0] + ',' + a[i][1]
      d3.timeout(function() {
        d3.select('#litiparts')
          .append('g')
          .attr('class', 'litismallparts')
          .attr('id', 'map-single-province-' + id + '-part' + k)
          .style('pointer-event', 'none')
          .append('path')
          .attr('d', partstr)
          // .attr('fill', 'rgb(200,200,200)')
          .attr('fill', 'black')
          .attr('stroke', 'none')
          .attr('opacity', 0.7)
      }, 50)
    },

    adjustCoordinate(provName, centerCoordinate) {
      if (provName == "河南省") {
        return [113.0668, 34.0818]; // [113.0668, 33.8818]
      }
      if (provName == "河北省") {
        return [115.35, 38.7];
      }
      if (provName == "内蒙古自治区") {
        return [114.32, 43.32];
      }
      if (provName == "陕西省") {
        return [108.44, 33.93];
      }
      if (provName == "甘肃省") {
        return [101.66, 39.00]; //[101.66, 38.25];
      }
      if (provName == "黑龙江省") {
        return [128.53, 47.01];
      }
      if (provName == "江苏省") {
        return [119.73, 33.12];
      }
      if (provName == "香港特别行政区") {
        return [116.12, 23.02]; //[115.12, 22.12];
      }
      if (provName == "澳门特别行政区") {
        return [113.54, 21.88];
      }
      if (provName == "广西壮族自治区") {
        return [108.0, 24.0];
      }
      if (provName == "广东省") {
        return [114.50, 24.5];
      }
      if (provName == "江西省") {
        return [115.5, 28.0];
      }
      if (provName == "上海市") {
        return [121.46485, 31.9671]; // [121.46485, 31.2671]
      }
      if (provName == "台湾省") {
        return [120.8254, 24.5986]; // [120.0254, 23.5986]
      }
      if (provName == "辽宁省") {
        return [122.8438, 41.5889]; // [122.0438, 41.0889]
      }
      if (provName == "北京市") {
        return [116.8, 40.76]; //[117.44, 38.83] // [117.48, 38.96] // [117.4219, 39.4189]
      }
      if (provName == "天津市") {
        return [117.49, 39.76]; //[117.44, 38.83] // [117.48, 38.96] // [117.4219, 39.4189]
      }
      if (provName == "重庆市") {
        // return [106.7463, 29.4667]; // [106.6663, 29.5367]
      }
      if (provName == "云南省") {
        return [101.0652, 24.8007]; // [101.0652, 25.1807]
      }
      return centerCoordinate;
    },
  }
};

</script>


