// 配置API接口地址
 var root = 'http://localhost:22097/'

// 引用axios
var axios = require('axios')
import iView from 'iview';
// 自定义判断元素类型JS
function toType(obj) {
  return ({}).toString.call(obj).match(/\s([a-zA-Z]+)/)[1].toLowerCase()
}
// 参数过滤函数
function filterNull(o) {
  for (var key in o) {
    if (o[key] === null) {
      delete o[key]
    }
    if (toType(o[key]) === 'string') {
      o[key] = o[key].trim()
    } else if (toType(o[key]) === 'object') {
      o[key] = filterNull(o[key])
    } else if (toType(o[key]) === 'array') {
      o[key] = filterNull(o[key])
    }
  }
  return o
}


function apiAxios(method, url, params, success, failure, baseURL) {
  // iView.LoadingBar.start();
  if (params) {
    params = filterNull(params)
  }

  if(!baseURL)
    baseURL = root

  axios({
      method: method,
      url: url,
      data: method === 'POST' || method === 'PUT' ? params : null,
      params: method === 'GET' || method === 'DELETE' ? params : null,
      baseURL: baseURL,
      withCredentials: false
    })
    .then(function(res) {

      if (res.data.success === true) {
        if (success) {
          success(res.data)

        }
      } else {
        if (failure) {
          failure(res.data)

        } else {


          console.error('error: ' + JSON.stringify(res.data))
        }
      }
    })
    .catch(function(err) {
      // let res = err.response
      if (err) {


          console.error('api error: ' + err)

      }
    })
}

export default {
  get: function(url, params, success, failure, baseURL) {
    return apiAxios('GET', url, params, success, failure, baseURL)
  },
  post: function(url, params, success, failure) {
    return apiAxios('POST', url, params, success, failure)
  },
  put: function(url, params, success, failure) {
    return apiAxios('PUT', url, params, success, failure)
  },
  delete: function(url, params, success, failure) {
    return apiAxios('DELETE', url, params, success, failure)
  }
}



