<template>
  <div class='ProvinceBar'>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import $ from "jquery";
import * as d3 from "d3";
import global from "@/constants/global"

export default {
  name: 'ProvinceBar',
  computed: {
    ...mapState(["thisYear","TotalProvince","SelectedProvince","SelectedInstitution","ProvinceData","InstitutionData","ProvinceFlag"]),
  },
  watch: {
    thisYear: function() {
      this.DrawProvinceBar()
    },
    SelectedProvince: function() {
      //this.DrawProvinceBar()
    },
    ProvinceData: function() {
      this.self.DrawProvinceBar()
    },
  },
  mounted() {
  },
  methods: {
    ...mapActions(["updateSelectedProvince"]),
    DrawProvinceBar(){
      //预处理数据
  let data = [];
  let self = this;
  $('#ProvinceBarsvg').remove();
  $('#ProvinceBarHead').text('中国境内注册人员省份分布')
  $('#ProvinceNum').text('参会省份：' + String(self.TotalProvince[self.thisYear]))
for (let name in self.ProvinceData[self.thisYear]) {
		if (self.ProvinceData[self.thisYear][name].count > 0) {
			let Property = []
			let dict = self.ProvinceData[self.thisYear][name]['Property']
			for (let i in global.Types) {
				if (dict.hasOwnProperty(global.Types[i])) {
					Property.push({
						'name': name,
						'type': global.Types[i],
						'value': dict[global.Types[i]]
					})
				}
			}
			data.push({
				'name': name,
				'count': self.ProvinceData[self.thisYear][name].count,
				'Property': Property
			})
		}
	}
	data = data.sort(this.compare("count")); //最多显示20个
	//定义尺寸
	var width = $("#rightcolumn1").width();
	var height = $("#rightcolumn1").width() * 0.34;
	let margin = {
		top: height * 0.13,
		right: width * 0.07,
		bottom: height * 0.1,
		left: width * 0.07
  };
	let svg = d3.select('#rightcolumn1')
		.append('svg') //	添加svg画布
		.attr('width', width)
		.attr('height', height)
		.attr("id", "ProvinceBarsvg")
		.append('g')
		.attr('transform', "translate(" + margin.left + "," + margin.top + ")");

	let xScale = d3.scaleBand() //定义x周缩放标尺函数
		.rangeRound([0, width - margin.left - margin.right]) //定义x轴的值域
		.paddingInner(0.05)
		.align(0.1)
		.domain(data.map(function (d) {
			return d.name
		}));

	let yScale = d3.scaleLinear() //定义y轴缩放标尺函数
		.range([height - margin.top - margin.bottom, 0])
		.domain([0, d3.max(data, function (d) {
			return d.count
		})])

	svg.append('g') //添加x坐标轴
		.attr('class', ' x axis')
		.attr('transform', "translate(0," + (height - margin.bottom - margin.top) + ")")
		.call(d3.axisBottom(xScale))
		.selectAll("text")
		.style("font-size", width / 70)
		.style("text-anchor", "start")
		.attr("transform", "translate(-10,0)");
	//.attr("transform", "rotate(45 -10 10)");

	svg.append('text') //添加x坐标轴的标题
		.attr('transform', function () {
			return `translate(${width - margin.right * 1.9},${height - margin.bottom * 2.2})`
		})
		.attr('fill', '#000')
		.attr('font-weight', 'bold')
		.text('省份');

	svg.append('g') //添加y坐标轴
		.attr('class', 'y axis')
		.call(d3.axisLeft(yScale).ticks(5))
		.append('text') //添加y坐标轴的标题
		.attr('fill', '#000')
		.attr('font-weight', 'bold')
		.attr('dx', width / 50)
		.attr('dy', -width / 60)
		.style('text-anchor', 'end')
		.text('人数');

	d3.selectAll("#ProvinceBarsvg .y.axis .tick > text") //修改y轴刻度的字体大小
		.style("font-size", function (d) {
			return width / 50
		});

	//添加纵轴
	svg.append('g')
		.attr('transform', function () {
			return `translate(${0},${0})`
		})
		.append('line')
		.attr('stroke', "currentColor")
		.attr('x1', 0)
		.attr('y1', -5)
		.attr('x2', 0)
		.attr('y2', height - margin.top - margin.bottom)

	//添加横轴
	svg.append('g')
		.attr('transform', function () {
			return `translate(${0},${height- margin.top - margin.bottom +0.5})`
		})
		.append('line')
		.attr('stroke', "currentColor")
		.attr('x1', 0)
		.attr('y1', 0)
		.attr('x2', width - margin.left - margin.right - 2)
		.attr('y2', 0)

	let bar = svg.selectAll('.bar') //添加每个列的g绘制，用于放单个的rect
		.data(data)
		.enter()
		.append('g')
		.attr("class", function (d) {
			return (d.name + " Provincebar")
		})/*
		.on('mouseover', function (d) {
			if (!self.SelectedProvince) {
				$(".state").removeClass("active");
				$("." + d.name + ".state").addClass("active");
			}
		})
		.on('mouseout', function () {
			$(".state").removeClass("active");
		})
		.on('click', function (d) {
			if (self.SelectedProvince != d.name) {
				$(".Provincebar").removeClass("active");
				$(this).addClass("active");
				self.SelectedProvince = d.name
				ChangeInstitution(self.thisYear, self.SelectedProvince, self.ProvinceData, InstitutionData)
				$("#TimeLinesvg").remove();
				$('#TimeLineHead').text(self.SelectedProvince + '注册人员' + TimeLineDict[TimeLineFlag] + '变化')
				DrawTimeLine(self.SelectedProvince, SelectedInstitution, self.ProvinceData, InstitutionData, TimeLineFlag)
				//$(".state").addClass("inactive");
				//$("."+d.name+".state").removeClass("inactive");
				$(".state").css("fill", Zerocolor);
				$("." + d.name + ".state").css("fill", GetColor(d.count));
			} else if (self.SelectedProvince == d.name) {
				returnChina(self.thisYear, self.ProvinceData, InstitutionData)
				$(".state").removeClass("active");
				$(".state").css("fill", function () {
					name = $(this).attr('class').replace(" state", "").replace(" active", "")
					$(this).css("fill", GetColor(self.ProvinceData[self.thisYear][name].count));
				})
			}
		});*/

	if (self.ProvinceFlag == "stacked") {
		let sum = 0
		bar.selectAll('.singlebar')
			//.attr('class', function (d) {
			//	return "bar bar--k" + String(RegionList.indexOf(d.region) + 1)
			//})
			.data(function (d) {
				return d.Property
			})
			.enter()
			.append("rect")
			.attr("class", function (d) {
				return (d.name + " singlebar " + d.type)
			})
			.attr('x', function (d) {
				return xScale(d.name);
			})
			.attr('y', function (d, i) {
				if (i == 0) {
					sum = 0
				}
				sum += d.value
				return yScale(sum);
			})
			.attr('width', xScale.bandwidth())
			.attr('height', function (d,i) {
				if (i == 0) {
					sum = 0
				}
				sum += d.value
				return height - margin.top - margin.bottom - yScale(d.value)
			})
			.on('mouseover', function (d) {
				d3.select("#ProvinceBarTooltip")
					.transition()
					.duration(200)
					.style("opacity", 0.9);
				d3.select("#ProvinceBarTooltip")
					.html(self.CreateTooltip2(d.type, d.name, d.value))
					.style("left", (d3.event.pageX) + "px")
					.style("top", (d3.event.pageY - 28) + "px");
			})
			.on('mouseout', function () {
				d3.select("#ProvinceBarTooltip")
					.transition()
					.duration(500)
					.style("opacity", 0);
			});

		var labHeight = width / 60;
		var labRadius = width / 150;
		let barLabel = svg.selectAll('.barLabel') //添加柱形条用recr矩形元素来绘制
			.data(global.Types)
			.enter()
			.append("g")
			.attr('class', 'barLabel')

		barLabel.append('rect')
			.attr('class', function (d, i) {
				return global.Types[i]
			})
			.attr("x", function (d) {
				return (width - margin.left - margin.right * 2.7);
			})
			.attr("y", function (d, i) {
				return (labHeight * i * 1.5);
			})
			.attr('width', width / 50)
			.attr('height', labHeight)
			.attr("rx", labRadius)

		barLabel.append("text")
			.style("font-size", width / 60)
			.attr('class', '.barLabelText') //添加柱形条用recr矩形元素来绘制
			.attr("x", function (d) {
				return (width - margin.left - margin.right * 2.2);
			})
			.attr("y", function (d, i) {
				return (labHeight * i * 1.5 + labHeight * 0.9);
			})
			.text(function (d) {
				return d;
			});

	} else if (self.ProvinceFlag == "align") {
		bar.append('rect')
			.attr('x', function (d) {
				return xScale(d.name);
			}).attr('y', function (d) {
				return yScale(d.count);
			})
			.attr('width', xScale.bandwidth())
			.attr('height', function (d) {
				return height - margin.top - margin.bottom - yScale(d.count)
			})
			.on('mouseover', function (d) {
				d3.select("#ProvinceBarTooltip")
					.transition()
					.duration(200)
					.style("opacity", 0.9);
				d3.select("#ProvinceBarTooltip")
					.html(self.CreateTooltip(d.name, d.count))
					.style("left", (d3.event.pageX) + "px")
					.style("top", (d3.event.pageY - 28) + "px");
			})
			.on('mouseout', function () {
				d3.select("#ProvinceBarTooltip")
					.transition()
					.duration(500)
					.style("opacity", 0);
			});
	}
    }
  }
};

</script>


