<template>
  <div class='InstitutionBar'>
  </div>
</template>
<script>
import $ from "jquery";
import * as d3 from "d3";
import { mapState, mapActions } from "vuex";
import global from "@/constants/global"

export default {
  name: 'InstitutionBar',
  computed: {
    ...mapState(["thisYear","SelectedProvince","SelectedInstitution","ProvinceData","InstitutionData"]),
  },
  watch: {
    thisYear: function() {
      this.DrawInstitutionBar()
    },
    SelectedProvince: function() {
      this.DrawInstitutionBar()
    },
    InstitutionData: function() {
      this.InstitutionData()
    },
  },
  mounted() {
  },
  methods: {
  ...mapActions(["updateSelectedInstitution"]),
  DrawInstitutionBar() { //提取数据
      //预处理数据
      let self = this
      let data = []
      let OriginData = {}
      $('#InstitutionBarsvg').remove();
      if (self.SelectedProvince != "") {
        OriginData = DictFilter(self.InstitutionData[self.thisYear], 'province', self.SelectedProvince)
        length = Object.keys(OriginData).length
        $('#InstitutionNum').text('参会单位：' + String(length) + ", 注册人数：" + String(self.ProvinceData[self.thisYear][self.SelectedProvince].count))
        $('#InstitutionBarHead').text(self.SelectedProvince + '注册人员单位Top20')
      } else {
        $('#InstitutionBarHead').text('中国境内注册人员单位Top20')
        $('#InstitutionNum').text('参会单位：' + String(Object.keys(self.InstitutionData[self.thisYear]).length))
        OriginData = self.InstitutionData[self.thisYear]
      }
      for (name in OriginData) {
        if (OriginData[name].count > 0) {
          data.push({
            'name': name,
            'count': OriginData[name].count,
            'province': OriginData[name].Province,
            'Property': OriginData[name].Property,
          })
        }
      }
      data = data.sort(this.compare("count")).slice(0, 20); //最多显示20个
      console.log(data)
      //定义尺寸
      var width = $("#rightcolumn2").width();
      var height = $("#rightcolumn2").width() * 0.42;
      let margin = {
        top: height * 0.1,
        right: width * 0.07,
        bottom: height * 0.24,
        left: width * 0.07
      };
      $('#InstitutionNum').text('参会单位：' + String(Object.keys(self.InstitutionData[self.thisYear]).length))
      let svg = d3.select('#rightcolumn2')
        .append('svg') //	添加svg画布
        .attr('width', width)
        .attr('height', height)
        .attr("id", "InstitutionBarsvg")
        .append('g')
        .attr('transform', "translate(" + margin.left + "," + margin.top + ")");

      let xScale = d3.scaleBand() //定义x周缩放标尺函数
        .rangeRound([0, width - margin.left - margin.right]) //定义x轴的值域
        .paddingInner(0.05)
        .align(0.1)
        .domain(data.map(function (d) {
          return d.name
        }));

      let yScale = d3.scaleLinear() //定义y轴缩放标尺函数
        .range([height - margin.top - margin.bottom, 0])
        .domain([0, d3.max(data, function (d) {
          return d.count
        })]) //y轴定义域

      svg.append('g') //添加x坐标轴
        .attr('class', ' x axis')
        .attr('transform', "translate(0," + (height - margin.bottom - margin.top) + ")")
        .call(d3.axisBottom(xScale))
        .selectAll("text")
        .style("font-size", width / 70)
        .style("text-anchor", "start")
        .attr('dx', -width / 100)
        .attr('dy', -width / 1500)
        .attr("transform", "rotate(40 -10 10)")

      svg.append('text') //添加x坐标轴的标题
        .attr('transform', function () {
          return `translate(${width - margin.right * 1.9},${height - margin.bottom * 1.3})`
        })
        .attr('fill', '#000')
        .attr('font-weight', 'bold')
        .text('单位');
      let max = Math.max.apply(Math, data.map(function (item) {
        return item.count
      }))

      let tick = Math.min(5, max);
      svg.append('g') //添加y坐标轴
        .attr('class', 'y axis')
        .call(d3.axisLeft(yScale).ticks(tick))
        .append('text') //添加y坐标轴的标题
        .attr('fill', '#000')
        .attr('font-weight', 'bold')
        .attr('dx', width / 50)
        .attr('dy', -width / 60)
        .style('text-anchor', 'end')
        .text('人数');

      d3.selectAll("#InstitutionBarsvg .y.axis .tick > text") //修改y轴刻度的字体大小
        .style("font-size", function (d) {
          return width / 50
        });

      let flag = 0;
      let limit = 60;
      if (xScale.bandwidth() > limit) flag = 1;
      svg.selectAll('.bar') //添加柱形条用recr矩形元素来绘制
        .data(data)
        .enter()
        .append('rect')
        .attr('x', function (d) {
          if (flag == 0) {
            return xScale(d.name);
          } else {
            return xScale(d.name) + xScale.bandwidth() / 2 - limit / 2;
          }
        })
        .attr('y', function (d) {
          return yScale(d.count);
        })
        .attr('width', function () {
          if (flag == 0) {
            return xScale.bandwidth()
          } else {
            return limit
          }
        })
        .attr('height', function (d) {
          return height - margin.top - margin.bottom - yScale(d.count)
        })
        .attr('class', function (d) {
          return d.Property
        })
        .on('mouseover', function (d) {
          d3.select("#InstitutionTooltip")
            .transition()
            .duration(200)
            .style("opacity", 0.9);
          d3.select("#InstitutionTooltip")
            .html(self.CreateTooltip(d.name, d.count))
            .style("position", "absolute")
            .style("left", (d3.event.pageX) + "px")
            .style("top", (d3.event.pageY - 28) + "px");
        })
        .on('mouseout', function () {
          d3.select("#InstitutionTooltip")
            .transition()
            .duration(500)
            .style("opacity", 0);
        })
        .on('click', function (d) {
          self.SelectedInstitution = d.name
         // $("#TimeLinesvg").remove();
         // $('#TimeLineHead').text(SelectedInstitution + '注册人员' + TimeLineDict[TimeLineFlag] + '变化')
         // DrawTimeLine(self.SelectedProvince, SelectedInstitution, self.ProvinceData, self.InstitutionData, TimeLineFlag)
        });

      //添加纵轴
      svg.append('g')
        .attr('transform', function () {
          return `translate(${0},${0})`
        })
        .append('line')
        .attr('stroke', "currentColor")
        .attr('x1', 0)
        .attr('y1', -5)
        .attr('x2', 0)
        .attr('y2', height - margin.top - margin.bottom)

      //添加横轴
      svg.append('g')
        .attr('transform', function () {
          return `translate(${0},${height- margin.top - margin.bottom +0.5})`
        })
        .append('line')
        .attr('stroke', "currentColor")
        .attr('x1', 0)
        .attr('y1', 0)
        .attr('x2', width - margin.left - margin.right - 2)
        .attr('y2', 0)
    }
      }
};

</script>


